import { Directive, TemplateRef, ViewContainerRef, Input } from '@angular/core';
import { AngularLazyscriptsService } from './angular-lazyscripts.service';

@Directive({
  selector: '[alsLoad]'
})
export class AngularLazyscriptsDirective {
  @Input() set alsLoad(url:string){
  this.alsService.loadExternalScript(url).then(()=>{
    this.vc.createEmbeddedView(this.template);
  }).catch(()=>{
    this.vc.clear();
  })

  }

  constructor(private template: TemplateRef<any>,private vc: ViewContainerRef,private alsService:AngularLazyscriptsService) { }

}
